import React from "react";
import Lottie from "react-lottie";

import { Retailer } from "../../../common/interfaces";
import cashbackActivationAnimation from "../../public/lotties/cashback-activation-animation.json";
import { RequestSource } from "../../../common/interfaces/login";

import './CashbackActivatedModal.scss';

const MODAL_AUTO_DISMISS_IN_MILLISECONDS = 3000;

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
interface CashbackActivatedModalProps {
    onRequestClose(isUserInitiated: boolean): void;
    cashbackActivatedFrom: RequestSource;
    retailer: Retailer;
}

interface CashbackActivatedModalState {

}

// ===========================================================================================
// --- CashbackActivated
// ===========================================================================================
export class CashbackActivatedModal extends React.Component<CashbackActivatedModalProps, CashbackActivatedModalState> {

    componentDidMount() {
        setTimeout(() => {
            this.closeModal(false);
        }, MODAL_AUTO_DISMISS_IN_MILLISECONDS);
    }

    closeModal = (isUserInitiated: boolean) => {
        this.props.onRequestClose(isUserInitiated);
    }

    render(): any {

        const canvas = {
            width: '450px',
        }

        const animation = {
            loop: true,
            autoplay: true,
            animationData: cashbackActivationAnimation,
            rendererSettings: {
                className: 'lottie',
                preserveAspectRatio: "xMidYMid slice"
            }
        };

        return (
            <div id="cashback-activated" className="canvas" style={canvas}>
                <button id="close-button" onClick={() => this.closeModal(true)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15">
                        <g fill="none" fill-rule="evenodd" stroke="#232E3B" stroke-linecap="round" stroke-width="2.36">
                            <path d="M2.289 1.3L13 13M12.711 1.3L2 13" />
                        </g>
                    </svg>
                </button>

                <Lottie height={160} options={animation} />

                <div className="cashback-activated-text">
                    <p>{this.props.retailer.activatedText}</p>
                </div>

                <div className="cashback-exclusion-text">

                    {this.props.cashbackActivatedFrom == RequestSource.CHECKOUT_MODAL &&
                        <p>Proceed to checkout</p>
                    }
                    {this.props.cashbackActivatedFrom != RequestSource.CHECKOUT_MODAL &&
                        <>
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="23" viewBox="0 0 24 23">
                                <g fill="none" fill-rule="evenodd" transform="translate(.5)">
                                    <circle cx="11.5" cy="11.5" r="11.5" fill="#FFBD46" />
                                    <rect width="2.752" height="8.807" x="10.124" y="4.619" fill="#FFF" rx="1.376" />
                                    <rect width="2.752" height="2.752" x="10.124" y="15.353" fill="#FFF" rx="1.376" />
                                </g>
                            </svg>
                            <p>Click the Earny browser icon for exclusions</p>
                        </>
                    }
                </div>
            </div>
        );
    }
}

