// ===========================================================================================
// --- EarnyWidget
// ===========================================================================================
class EarnyWidget extends React.Component<EarnyWidgetProps, EarnyWidgetState> {
    private widget: RefObject<any> = React.createRef();

    constructor(props: EarnyWidgetProps) {
        super(props);

        this.state = {
            isLoggedIn: false,
            hasSeenStartupModal: false,
            showWidget: false,
            showContent: false,
            dragging: false
        };
    }

    componentWillMount() {
        document.addEventListener('mousedown', this.handleClick, false);
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClick, false);
    }

    async componentDidMount() {

        if (chrome && chrome.runtime.onMessage) {
            chrome.runtime.onMessage.addListener(async (request, sender, sendResponse) => {
                if (request.type === EY.ModalActionTypes.TYPE_SHOW_WIDGET) {
                    await this.presentWidgetComponent();
                    return sendResponse({ success: true });
                }

                else if (request.type === EY.ModalActionTypes.TYPE_MINIMIZE_WIDGET) {
                    this.close()
                    return sendResponse({ success: true });
                }

                else if (request.type === EY.ModalActionTypes.TYPE_CLOSE_WIDGET) {
                    await this.dismissWidgetComponent();

                    this.setState({ showWidget: false, showContent: false });
                    return sendResponse({ success: true });
                }

                return sendResponse({ success: false });
            });
        }

        const root = $("#earny-widget-root");
        root.draggable({ axis: "y", containment: 'document' });
    }

    handleClick = (e: any) => {
        if (this.widget.current && !this.widget.current.contains(e.target)) {
            this.close();
        }
    }

    presentWidgetComponent = async () => {
        this.setState({ showWidget: true });
    }

    dismissWidgetComponent = async () => {
        this.setState({ showWidget: false, showContent: false });
    }


    open = async () => {
        // If already open do nothing
        if (this.state.showContent) {
            return;
        }
        // Keep it non blocking
        ContentScriptMixpanelService.trackEarnyWidgetOpened();
        const isLoggedIn: boolean = await MessageSender.isUserLoggedIn();
        const hasSeenStartupModal: boolean = await browserService.getStartupModalShown()
        this.setState({ showContent: true, isLoggedIn, hasSeenStartupModal });
    }

    close = () => {
        this.setState({ showContent: false });
    }

    render() {
        if (this.state.showWidget === false) {
            return (<></>);
        }

        return (
            <div id="widget" ref={this.widget}>
                <div id="button-wrapper" >
                    <div id="button-wrapper-right" onClick={this.open}>
                        <WidgetButton />
                    </div>
                </div>

                {this.state.showContent &&
                    <div id="content-wrapper" className={this.state.showContent ? 'earny-show' : 'earny-hide'}>
                        <WidgetContent
                            pressedCloseButton={this.close}
                            isLoggedIn={this.state.isLoggedIn}
                            hasSeenStartupModal={this.state.hasSeenStartupModal} />
                    </div>
                }
            </div>
        );
    }
}

export default EarnyWidget;