import React from "react";
import { Retailer } from "../../../common/interfaces";
import { ContentScriptMixpanelService } from "../../services/content-script-mixpanel.service";
import { MessageSender } from '../../../common/messaging/message-sender';
import { RequestSource } from "../../../common/interfaces/login";

import './NoProductDetailsPageModal.scss';

interface NoProductDetailsPageModalProps {
    retailer: Retailer | null,
    addToPendingProductDetails: () => void,
    onRequestClose: () => void;
}

interface NoProductDetailsPageModalState {

}

export class NoProductDetailsPageModal extends React.Component<NoProductDetailsPageModalProps, NoProductDetailsPageModalState> {

    activateCashback = async () => {
        ContentScriptMixpanelService.trackActivateCashbackPressed(this.props.retailer!);
        MessageSender.activateCashbackForActiveRetailer(RequestSource.POPUP, true);
    }

    render() {
        const canvas = {
            width: '408px',
            height: '100%',
            padding: '30px'
        }

        const retailer: Retailer | null = this.props.retailer;

        return (
            <div id="no-product-details-page" className="canvas" style={canvas}>
                <div id="close-button" onClick={this.props.onRequestClose}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15">
                        <g fill="none" fill-rule="evenodd" stroke="#232E3B" stroke-linecap="round" stroke-width="2.36">
                            <path d="M2.289 1.3L13 13M12.711 1.3L2 13" />
                        </g>
                    </svg>
                </div>

                <p className="add-to-view-details-title">Add this product to your watchlist to view details</p>

                <p className="information-title">Information will be updated within the hour</p>

                <button id="add-to-watchlist" onClick={this.props.addToPendingProductDetails}>
                    Add to Watchlist
                </button>

                {retailer && !retailer.isCashbackActivated &&
                    <button id="activate-cashback" onClick={this.activateCashback}>
                        {retailer.buttonText}
                    </button>
                }

            </div>
        );
    }
}