import React from "react";

import { Product, Retailer } from "../../../common/interfaces";
import { ProductDetailsPageModal } from "../ProductDetailsPage/ProductDetailsPageModal";
import { ProductDetailsPendingModal } from "../ProductDetailsPendingPage/ProductDetailsPendingModal";
import { NoProductDetailsPageModal } from '../NoProductDetailsPage/NoProductDetailsPageModal';
import { MessageSender } from "../../../common/messaging/message-sender";
import { ProductState, ProductStateParser } from "../../../common/product-state-parser/product-state-parser";
import { WidgetLoginModal } from "../Login/WidgetLoginModal";
import { StartupModal } from "../StartupScreen/StartupModal";
import { BrowserService } from "../../../common/services/browser.service";

import './WidgetContent.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================

interface WidgetContentProps {
    isLoggedIn: boolean;
    hasSeenStartupModal: boolean;
    pressedCloseButton: () => void
}

interface WidgetContentState {
    loaded: boolean;
    isLoggedIn: boolean;
    hasSeenStartupModal: boolean;
    activeProduct: Product | null;
    activeRetailer: Retailer | null;
    isPending: boolean;
}

const browserService = new BrowserService();

// ===========================================================================================
// --- WidgetContent
// ===========================================================================================
export class WidgetContent extends React.Component<WidgetContentProps, WidgetContentState> {


    constructor(props: WidgetContentProps) {
        super(props);

        this.state = {
            loaded: false,
            isLoggedIn: this.props.isLoggedIn,
            hasSeenStartupModal: this.props.hasSeenStartupModal,
            activeProduct: null,
            activeRetailer: null,
            isPending: false,
        };
    }

    async componentDidMount() {
        await this.populateData();
    }

    populateData = async () => {
        const newState: WidgetContentState = {} as any;

        // Retrieve product and retailer
        const [product, retailer]: [Product | null, Retailer | null] = await Promise.all([
            MessageSender.getActiveProduct(),
            MessageSender.getActiveRetailer()
        ]);

        newState.activeProduct = product;
        newState.activeRetailer = retailer;
        newState.loaded = true;

        return this.setState(newState);
    }

    onStartupClose = async () => {
        await browserService.setStartupModalShown();
        this.setState({ hasSeenStartupModal: true });
    }

    getModalToDisplay = () => {
        const productState: ProductState = ProductStateParser.getState(this.state.activeProduct);

        if (this.state.hasSeenStartupModal === false) {
            return (<StartupModal onRequestClose={this.onStartupClose} />);
        }

        if (this.state.isLoggedIn === false) {
            return (<WidgetLoginModal onRequestClose={this.props.pressedCloseButton} />);
        }

        if (productState === ProductState.PRODUCT_DETAILS_PENDING || this.state.isPending) {
            return (<ProductDetailsPendingModal onRequestClose={this.props.pressedCloseButton} />);
        }

        if (productState === ProductState.NO_PRODUCT_DETAILS) {
            return (
                <NoProductDetailsPageModal
                    retailer={this.state.activeRetailer}
                    onRequestClose={this.props.pressedCloseButton}
                    addToPendingProductDetails={this.addProductToPendingProductDetails}
                />
            );
        }

        if (productState === ProductState.PRODUCT_DETAILS_VALID) {
            return (
                <ProductDetailsPageModal
                    product={this.state.activeProduct!}
                    onRequestClose={this.props.pressedCloseButton}
                    onWatchlistUpdated={this.onWatchlistUpdated}
                />
            );
        }

        return (
            <NoProductDetailsPageModal
                retailer={this.state.activeRetailer}
                onRequestClose={this.props.pressedCloseButton}
                addToPendingProductDetails={this.addProductToPendingProductDetails}
            />
        );
    }

    onWatchlistUpdated = async (isInWatchlist: boolean, isFromProductDetails: boolean) => {
        if (isInWatchlist) {
            await MessageSender.addItemToWatchlist(isFromProductDetails)
        } else {
            await MessageSender.removeItemFromWatchlist(isFromProductDetails);
        }

        const activeProduct = this.state.activeProduct;

        if (activeProduct && activeProduct.productDetails) {
            activeProduct.productDetails.isInWatchlist = isInWatchlist;
            this.setState({ activeProduct });
        }
    }

    addProductToPendingProductDetails = async () => {
        if (await MessageSender.addItemToWatchlistPendingProductDetails() === true) {
            this.setState({ isPending: true });
        }
    }

    render() {
        let content = <></>;

        if (this.state.loaded) {
            content = this.getModalToDisplay();
        }

        return (
            <div id="earny-widget-content">
                {content}
            </div>
        );
    }
}