import React from "react";
import Lottie from "react-lottie";
import checkoutCouponAnimation from '../../public/lotties/checkout-coupon-animation.json';
import { CouponProgressBar } from "./CouponProgressBar/CouponProgressBar";
import { ContentScriptMixpanelService } from "../../services/content-script-mixpanel.service";
import { Retailer } from "../../../common/interfaces/index.js";

import './CouponModal.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================

interface CouponModalProps {
    onRequestClose: (isUserInitiated: boolean) => void;
    retailer: Retailer;
}

interface CouponModalState {
    isCouponCheckComplete: boolean;
    couponCheckProgress: number;
}

// ===========================================================================================
// --- CouponModal
// ===========================================================================================
export class CouponModal extends React.Component<CouponModalProps, CouponModalState> {

    private progress: any = null;

    constructor(props: any) {
        super(props);

        this.state = {
            isCouponCheckComplete: false,
            couponCheckProgress: 0
        };
    }

    componentDidMount() {
        this.progress = setTimeout(() => {
            randomProgress(0, 100, 5, 25, 250, 2000, (value: number) => {
                this.setState({ couponCheckProgress: value });

                if (value === 100 && this.progress !== null) {
                    ContentScriptMixpanelService.trackCouponTestingScreenFinishedLoading();
                    this.setState({ isCouponCheckComplete: true });
                }
            })
        }, 100);
    }

    close = () => {
        if (!this.state.isCouponCheckComplete) {
            ContentScriptMixpanelService.trackCouponTestingScreenDismissed();
        } else {
            ContentScriptMixpanelService.trackCouponResultScreenDismissed();
        }
        clearTimeout(this.progress);
        this.progress = null;
        this.props.onRequestClose(true);
    }

    goToCheckout = () => {
        ContentScriptMixpanelService.trackCouponResultScreenContinueToCheckoutButtonPressed();
        this.props.onRequestClose(false);
    }

    render() {
        const animation = {
            loop: true,
            autoplay: true,
            animationData: checkoutCouponAnimation,
            rendererSettings: {
                className: 'lottie',
                preserveAspectRatio: "xMidYMid slice"
            }
        };

        return (
            <div id="coupon-modal">
                <svg id="earny-logo" xmlns="http://www.w3.org/2000/svg" width="72" height="30" viewBox="0 0 72 30">
                    <g fill="none" fill-rule="evenodd">
                        <path fill="#20282F" d="M3.343 7.99c0-.433.354-.785.793-.787h8.21c.926 0 1.644-.883 1.34-1.844-.183-.578-.757-.947-1.369-.947H4.15C2.356 4.412.521 5.42.521 7.643v12.95c0 2.221 1.835 3.23 3.628 3.23h8.168c.612 0 1.186-.369 1.369-.946.304-.962-.414-1.845-1.34-1.845h-8.21c-.439-.001-.793-.353-.793-.787v-4.728h7.9c.612 0 1.186-.369 1.368-.946.04-.126.061-.25.068-.372.002-.027 0-.054 0-.081 0-.027.002-.055 0-.082-.007-.12-.028-.245-.068-.371-.182-.578-.756-.947-1.368-.947h-7.9V7.99M23.441 20.887c-1.59.26-4.1.403-4.773-1.04 0 0-.99-1.924 1.05-2.67 0 0 1.866-.654 5.27.335-.087 2.7-.22 3.158-1.547 3.375zM17.923 9.08c-.775.158-1.328.803-1.214 1.55.114.748.841 1.267 1.624 1.159 0 0 1.817-.468 4.738-.017.597.1 1.917.735 1.901 3.693-1.32-.271-4.727-.937-6.33-.404-1.891.63-2.51 1.575-2.84 2.36-.685 1.628.075 3.224.265 3.58.991 2.073 3.199 2.823 5.325 2.823 1.09 0 2.72-.09 3.596-.395 2.557-.893 2.886-2.782 2.886-7.326 0-4.856-1.185-5.859-3.701-6.858 0 0-2.996-.827-6.25-.165zM37.386 8.999c-.283-.069-2.822-.63-4.813.702-1.085.725-2.052 1.977-2.032 3.338l-.02 9.34c0 .614.371 1.19.952 1.374.965.304 1.851-.416 1.851-1.345V13.7c-.054-.708.382-1.359.787-1.633.83-.562 2.18-.417 2.63-.313.752.177 1.504-.292 1.683-1.05.18-.761-.285-1.525-1.038-1.706M46.4 8.824c-4.371-.032-5.945 2.726-6.159 4.253l-.014 9.331c0 .782.621 1.416 1.388 1.416.766 0 1.387-.634 1.387-1.416v-8.171c0-.534.625-2.478 2.995-2.58.018 0 .036.003.054.002.125-.007.24-.007.357-.008.114.001.226.001.349.008.018.001.035-.003.053-.002 2.407.103 2.996 1.92 2.996 2.57v8.181c0 .782.62 1.416 1.387 1.416.766 0 1.387-.634 1.387-1.416v-9.13l-.014-.2c-.214-1.528-1.792-4.29-6.166-4.254M66.523 8.864c-.745-.184-1.498.274-1.682 1.023l-2.722 11.111c-1.607.649-2.347-.613-2.347-.613L57.08 9.873c-.191-.747-.948-1.196-1.693-1.005-.743.192-1.19.954-.999 1.701l2.747 10.712c.315.905 1.428 2.45 3.6 2.494.265.006.504.01.723.012-1.048 3.086-3.393 3.395-3.705 3.422-.759.054-1.335.713-1.289 1.479.045.741.657 1.312 1.386 1.312.028 0 .057 0 .086-.003 1.936-.118 5.478-1.839 6.636-7.331l2.967-12.11c.183-.75-.272-1.507-1.017-1.692" />
                        <path fill="#FFBD46" d="M69.345 20.294c-.975 0-1.765.79-1.765 1.765 0 .974.79 1.765 1.765 1.765.975 0 1.765-.79 1.765-1.765 0-.975-.79-1.765-1.765-1.765" />
                        <g fill="#20282F">
                            <path d="M6.038.167c.715.025 1.436.026 2.124.258 1.263.425 2.26 1.248 3.007 2.407.373.582.52 1.244.485 1.95-.015.297-.13.43-.405.39-.26-.037-.519-.11-.768-.2-.548-.198-1.089-.42-1.635-.624-2.16-.81-4.295-.723-6.41.198-.546.238-1.093.477-1.682.571-.39.062-.619-.126-.66-.541-.103-1.023.29-1.833.957-2.522C1.798 1.28 2.683.779 3.685.478 4.46.245 5.245.19 6.038.167M29.476.153c.666.01 1.332.035 1.978.217.935.263 1.726.8 2.416 1.517.47.487.869 1.02 1.075 1.698.115.376.159.76.143 1.153-.014.36-.168.511-.492.414-.6-.18-1.202-.364-1.781-.607-1.112-.466-2.252-.76-3.448-.767-1.16-.008-2.275.251-3.359.692-.56.228-1.136.415-1.707.614-.083.029-.178.029-.267.027-.265-.004-.428-.133-.48-.41-.106-.56-.023-1.099.204-1.612.294-.666.78-1.157 1.322-1.586C26.08.713 27.22.33 28.448.21c.341-.034.685-.039 1.028-.056" transform="translate(16.404)" />
                        </g>
                    </g>
                </svg>

                <div id="close-button" onClick={this.close}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15">
                        <g fill="none" fill-rule="evenodd" stroke="#232E3B" stroke-linecap="round" stroke-width="2.36">
                            <path d="M2.289 1.3L13 13M12.711 1.3L2 13" />
                        </g>
                    </svg>
                </div>

                {!this.state.isCouponCheckComplete &&
                    <div className="coupon-checking">
                        <Lottie options={animation} />

                        <div id="animation"></div>

                        <h3>Testing coupons...</h3>
                        <CouponProgressBar width={500} progress={this.state.couponCheckProgress} />
                    </div>
                }

                {this.state.isCouponCheckComplete &&
                    <div className="coupon-complete">
                        <h3>You’ve got the best price</h3>
                        <p>We didn’t find any active coupons, but have applied a cashback offer.</p>

                        <div className="cashback">
                            <div className="title">Cashback</div>
                            <div className="value">{this.props.retailer.cashbackRate} activated</div>
                        </div>

                        <button className="cta" onClick={this.goToCheckout}>
                            Continue to checkout
                            </button>
                    </div>
                }

            </div>
        );
    }
}

async function randomProgress(minValue: number, maxValue: number, minIncrease: number,
    maxIncrease: number, minWaitMs: number, maxWaitMs: number, callback: (value: number) => void, value?: number): Promise<void> {
    let currentValue = value || minValue;
    callback(currentValue);
    if (currentValue === maxValue) return;
    const randomIncrease = Math.floor(Math.random() * maxIncrease) + minIncrease;
    currentValue += (currentValue + randomIncrease > maxValue) ? (maxValue - currentValue) : randomIncrease;
    await new Promise(r => setTimeout(r, (Math.random() * maxWaitMs) + minWaitMs));
    await randomProgress(minValue, maxValue, minIncrease, maxIncrease, minWaitMs, maxWaitMs, callback, currentValue)
}