import EY from "../../common/EY";
import { Retailer } from "../../common/interfaces";
import { MessageSender } from "../../common/messaging/message-sender";
import { Formatters } from "../../common/utils/formatters";

export class ContentScriptMixpanelService {

    /***
     * Nothing is being tracked directly from the content-script, the background script handles it
     * so we send it a message to request a track
     */
    private static async requestTrack(eventName: string, data?: any) {
        try {
            const payload = { viewType: EY.ChromeViewTypes.CHROME_VIEW_TYPE_MODAL, ...data };
            await MessageSender.requestTrackMixpanelEvent(eventName, payload);
        } catch (error) {
            // console.error(`Error tracking mixpanel event ${eventName}`, error.message);
        }
    }

    public static trackLoginButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_LOGIN_BUTTON_PRESSED);
    }

    public static trackSignupButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_SIGNUP_BUTTON_PRESSED);
    }

    public static trackAddToWatchlistItemCtaButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_ADD_TO_WATCHLIST_ITEM_CTA_BUTTON_PRESSED);
    }

    public static trackAddToWatchlistItemRemoveButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_ADD_TO_WATCHLIST_ITEM_REMOVE_BUTTON_PRESSED);
    }

    public static trackProductDetailsAddToWatchlistCTAButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_PRODUCT_DETAILS_ADD_TO_WATCHLIST_CTA_BUTTON_PRESSED);
    }

    public static trackProductDetailsAddToWatchlistRemoveButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_PRODUCT_DETAILS_REMOVE_ITEM_FROM_WATCHLIST_CTA_PRESSED);
    }

    public static trackiOSAppStoreButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_IOS_APP_STORE_BUTTON_PRESSED);
    }

    public static trackAndroidAppStoreButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_ANDROID_APP_STORE_BUTTON_PRESSED);
    }

    public static trackActivateCashbackPressed(retailer: Retailer) {
        const payload = Formatters.formatRetailerForMixpanel(retailer);
        this.requestTrack(EY.MixpanelEvents.EVENT_ACTIVATE_CASHBACK_BUTTON_PRESSED, payload);
    }

    public static trackActivateCashbackUpsellPressed(retailer: Retailer) {
        const payload = Formatters.formatRetailerForMixpanel(retailer);
        this.requestTrack(EY.MixpanelEvents.EVENT_ACTIVATE_CASHBACK_UPSELL_BUTTON_PRESSED, payload);
    }

    public static trackPressedViewPriceHistory() {
        this.requestTrack(EY.MixpanelEvents.EVENT_PRESSED_VIEW_PRICE_HISTORY);
    }

    public static trackEarnyWidgetOpened() {
        this.requestTrack(EY.MixpanelEvents.EVENT_EARNY_WIDGET_OPENED);
    }

    public static trackModalDismissed(modalName: string) {
        this.requestTrack(EY.MixpanelEvents.EVENT_MODAL_DISMISSED, { 'Modal Name': modalName });
    }

    public static trackCheckoutModalApplyButtonPressed(retailer: Retailer) {
        const payload = Formatters.formatRetailerForMixpanel(retailer);
        this.requestTrack(EY.MixpanelEvents.EVENT_CHECKOUT_MODAL_APPLY_BUTTON_PRESSED, payload);
    }

    public static trackCouponTestingScreenFinishedLoading() {
        this.requestTrack(EY.MixpanelEvents.EVENT_COUPON_TESTING_SCREEN_FINISHED_LOADING);
    }

    public static trackCouponTestingScreenDismissed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_COUPON_TESTING_SCREEN_DISMISSED);
    }

    public static trackCouponResultScreenDismissed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_COUPON_RESULT_SCREEN_DISMISSED);
    }

    public static trackCouponResultScreenContinueToCheckoutButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_COUPON_RESULT_SCREEN_CONTINUE_TO_CHECKOUT_BUTTON_PRESSED);
    }
}


