import React, { RefObject } from "react";
import { GradientStaticSlider } from "../../../common/react/GradientStaticSlider/GradientStaticSlider";
import { PriceHistoryChart } from "./PriceHistoryChart/PriceHistoryChart";
import WatchListConfirmationModal, { WatchlistConfirmationModalPosition } from "../WatchListConfirmationModal/WatchListConfirmationModal";
import { ContentScriptMixpanelService } from '../../services/content-script-mixpanel.service';
import { Product, ProductDetails, PriceDetails } from "../../../common/interfaces";

import './ProductDetailsPageModal.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
interface ProductDetailsPageProps {
    product: Product;
    onRequestClose: () => void;
    onWatchlistUpdated: (isInWatchlist: boolean, isFromProductDetails: boolean) => void;
}

interface ProductDetailsPageState {
    isProductSavedToWatchlist: boolean;
}

// ===========================================================================================
// --- ProductDetailsPageModal
// ===========================================================================================
export class ProductDetailsPageModal extends React.Component<ProductDetailsPageProps, ProductDetailsPageState> {

    private modal: RefObject<WatchListConfirmationModal> = React.createRef();

    constructor(props: ProductDetailsPageProps) {
        super(props);

        this.state = {
            isProductSavedToWatchlist: this.props.product.productDetails!.isInWatchlist
        };
    }

    onSaveToWatchlist = async () => {
        const isProductSavedToWatchlist = !this.state.isProductSavedToWatchlist;
        this.props.onWatchlistUpdated(isProductSavedToWatchlist, true);
        this.setState({ isProductSavedToWatchlist });

        if (isProductSavedToWatchlist) {
            if (this.modal.current) {
                this.modal.current.show();
            }
            ContentScriptMixpanelService.trackProductDetailsAddToWatchlistCTAButtonPressed();
        } else {
            if (this.modal.current) {
                this.modal.current.close();
            }
            ContentScriptMixpanelService.trackProductDetailsAddToWatchlistRemoveButtonPressed();
        }
    }

    render(): any {

        const canvas = {
            width: '370px',
            height: '100%',
            padding: '20px'
        }

        if (!this.props.product || !this.props.product.productDetails) {
            return (<></>);
        }

        const productDetails: ProductDetails = this.props.product.productDetails;
        const priceDetails: PriceDetails | undefined = this.props.product.priceDetails;

        let productName: string = productDetails.name || '';
        productName = (productName.length >= 42) ? (productName.substring(0, 42) + '...') : productName;
        const lowestPriceTitle: string = (priceDetails && priceDetails.lowestPrice) ? priceDetails.lowestPrice.title : 'LOWEST';
        const highestPriceTitle: string = (priceDetails && priceDetails.highestPrice) ? priceDetails.highestPrice.title : 'HIGHEST';
        let lowestPriceValue: string = (priceDetails && priceDetails.lowestPrice) ? priceDetails.lowestPrice.value.toString() : 'N/A';
        let highestPriceValue: string = (priceDetails && priceDetails.highestPrice) ? priceDetails.highestPrice.value.toString() : 'N/A';

        if (lowestPriceValue[0] !== '$') lowestPriceValue = '$' + lowestPriceValue;
        if (highestPriceValue[0] !== '$') highestPriceValue = '$' + highestPriceValue;

        return (
            <div id="product-details-modal" className="canvas" style={canvas}>
                <div id="close-button" onClick={this.props.onRequestClose}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15">
                        <g fill="none" fill-rule="evenodd" stroke="#232E3B" stroke-linecap="round" stroke-width="2.36">
                            <path d="M2.289 1.3L13 13M12.711 1.3L2 13" />
                        </g>
                    </svg>
                </div>

                <div className="product-details">
                    <img src={productDetails.imageUrl} alt="" />
                    <div className="product-content">
                        <div className="product-name">
                            <p>{productName}</p>
                        </div>
                        <div className="product-prices">
                            {productDetails.price &&
                                <div className="product-new-price">
                                    {productDetails.price}
                                </div>
                            }
                            <button id="save-button" className={this.state.isProductSavedToWatchlist ? 'saved' : 'unsaved'} onClick={this.onSaveToWatchlist}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="23" height="20" viewBox="0 0 26 22">
                                    <path id="save-path" fill="none" fill-rule="evenodd" stroke="#232E3B" stroke-width="2" d="M20.407 1.487c-4.558-2.013-7.783 2.888-7.783 2.888S9.399-.525 4.84 1.484c-3.565 1.575-4.879 5.97-2.957 9.36 1.48 2.615 4.512 5.908 10.21 9.986a.915.915 0 0 0 1.062 0c5.695-4.074 8.727-7.371 10.208-9.982 1.92-3.391.604-7.786-2.957-9.361z" />
                                </svg>
                            </button>
                        </div>
                    </div>
                </div>

                {priceDetails && priceDetails.timeToBuy &&
                    <div className="product-time-to-buy">
                        <span className="left">Wait</span>
                        <GradientStaticSlider percentage={priceDetails.timeToBuy.value} />
                        <span className="right">Buy</span>
                    </div>
                }

                <div id="price-stats">
                    <div className="price-stat left">
                        <p>{highestPriceTitle}</p>
                        <p className="price-stat-price">{highestPriceValue}</p>
                    </div>

                    <div className="price-stat right">
                        <p>{lowestPriceTitle}</p>
                        <p className="price-stat-price">{lowestPriceValue}</p>
                    </div>
                </div>

                <PriceHistoryChart chartData={priceDetails ? priceDetails.chartData : undefined} />

                <WatchListConfirmationModal ref={this.modal} checkMarkIconSize={70} position={WatchlistConfirmationModalPosition.Bottom} />
            </div>
        );
    }
}