import React from "react";
import Lottie from "react-lottie";
import { ContentScriptMixpanelService } from '../../services/content-script-mixpanel.service';
import { MessageSender } from "../../../common/messaging/message-sender";
import { RequestSource } from "../../../common/interfaces/login";

import amazonAnimation from '../../public/lotties/amazon-watchlist-animation.json';

import './WidgetLoginModal.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
interface LoginModalProps {
    onRequestClose(): void;
}

interface LoginModalState {

}

// ===========================================================================================
// --- WidgetLoginModal
// ===========================================================================================
export class WidgetLoginModal extends React.Component<LoginModalProps, LoginModalState> {

    handleLoginClick = (e: any) => {
        ContentScriptMixpanelService.trackLoginButtonPressed();
        MessageSender.goToLogin(false, RequestSource.WIDGET);
        this.closeModal();
    }

    handleSignupClick = (e: any) => {
        ContentScriptMixpanelService.trackSignupButtonPressed();
        MessageSender.goToLogin(true, RequestSource.WIDGET);
        this.closeModal();
    }

    closeModal = () => {
        this.props.onRequestClose();
    }

    render() {
        const canvas = {
            width: '408px',
            height: '100%',
        }

        const animation = {
            loop: true,
            autoplay: true,
            animationData: amazonAnimation,
            rendererSettings: {
                className: 'lottie',
                preserveAspectRatio: "xMidYMid slice"
            }
        };

        return (
            <div id="login-modal" className="canvas" style={canvas}>

                <Lottie options={animation} height={160} width={408} />

                <div id="login-modal-content">
                    <p className="watchlist-title">Now add products to your watchlist</p>

                    <p className="watchlist-extra">Prices fluctuate daily. Track price drops, view price history, and more!</p>

                    <div id="login-buttons">
                        <button id="signin-button" className="left" onClick={this.handleLoginClick}>Log In</button>
                        <button id="signup-button" className="right" onClick={this.handleSignupClick}>Sign Up</button>
                    </div>
                </div>

            </div>
        );
    }
}
