import React from 'react';
import ReactDOM from 'react-dom';
import EarnyModal from './earny-modals/EarnyModal';
import EarnyWidget from './earny-widget/EarnyWidget';

import './scss/fonts.scss';

const $ = require('jquery');

$(document).ready(function () {
    const contentScript = document.createElement('div');
    const contentScriptEarnyWidget = document.createElement('div');

    contentScript.id = "earny-root";
    contentScriptEarnyWidget.id = "earny-widget-root";

    contentScript.className = "earny-scope";
    contentScriptEarnyWidget.className = "earny-scope";

    document.body.appendChild(contentScript);
    document.body.appendChild(contentScriptEarnyWidget);

    ReactDOM.render(<EarnyModal />, contentScript);
    ReactDOM.render(<EarnyWidget />, contentScriptEarnyWidget);
});