import React from "react";
import Lottie from "react-lottie";

import earnyWidgetAnimation from '../../public/lotties/earny-widget-drag-animation.json';

import './StartupModal.scss';


interface StartupScreenProps {
    onRequestClose: () => void;
}

interface StartupScreenState {

}

export class StartupModal extends React.Component<StartupScreenProps, StartupScreenState> {

    render() {
        const canvas = {
            width: '408px',
            height: '100%',
            padding: '30px'
        }

        const animation = {
            loop: true,
            autoplay: true,
            animationData: earnyWidgetAnimation,
            rendererSettings: {
                className: 'lottie',
                preserveAspectRatio: "xMidYMid slice"
            }
        };

        return (

            <div id="startup-screen-modal" className="canvas" style={canvas}>


                <Lottie options={animation} height={201} width={321} />

                <p className="drag-it-title">Drag and place where you like it</p>

                <p className="description">No one likes being in the way. Browse distraction-free.</p>

                <button id="confirm-button" onClick={this.props.onRequestClose}>Got it</button>

            </div>

        );
    }
}