// ===========================================================================================
// --- ActivateCashbackModal
// ===========================================================================================
export class ActivateCashbackModal extends React.Component<ActivateCashbackModalProps, ActivateCashbackModalState> {

    closeModal = (isUserInitiated: boolean) => {
        this.props.onRequestClose(isUserInitiated);
    }

    activateCashbackPressed = async () => {
        this.closeModal(false);
        ContentScriptMixpanelService.trackActivateCashbackPressed(this.props.retailer);
        const source = RequestSource.ACTIVATE_CASHBACK_MODAL;
        const showConfirmation = true;
        await MessageSender.activateCashbackForActiveRetailer(source, showConfirmation);
    }

    activateDoubleCashbackPressed = async () => {
        ContentScriptMixpanelService.trackActivateCashbackUpsellPressed(this.props.retailer);
        const source = RequestSource.ACTIVATE_CASHBACK_MODAL;
        const showConfirmation = false;
        await MessageSender.activateDoubleCashbackForActiveRetailer(source, showConfirmation);
        this.closeModal(false);
    }

    render(): any {
        return (
            <div id="activate-cashback" className="canvas">

                {this.props.isLoggedIn && this.props.isSubscribed &&
                    <EarnyPlusLogo />
                }

                {(!this.props.isLoggedIn || !this.props.isSubscribed) &&
                    <EarnyLogo />
                }

                <div className="center">

                    <div className="retailer-name">
                        {this.props.retailer.retailerImageUrlSquare &&
                            <img src={this.props.retailer.retailerImageUrlSquare} />
                        }
                        <span>{this.props.retailer.name}</span>
                    </div>


                    {!this.props.isLoggedIn &&
                        <div>
                            <button id="activate-cashback-button" onClick={this.activateCashbackPressed}>{this.props.retailer.activateCashbackSubscribedCTA || this.props.retailer.buttonText}</button>
                        </div>
                    }

                    {this.props.isLoggedIn &&
                        <>
                            <div>
                                <button id="activate-cashback-button" onClick={this.activateCashbackPressed}>{this.props.retailer.buttonText}</button>
                            </div>

                            {!this.props.isSubscribed && this.props.retailer.activateCashbackSubscribedCTA &&
                                <div className="activate-double-cashback">
                                    <button id="activate-double-cashback-button" onClick={this.activateDoubleCashbackPressed}>
                                        {this.props.retailer.activateCashbackSubscribedCTA}
                                    </button>
                                </div>
                            }
                        </>
                    }

                </div>

                <button id="close-button-right" onClick={() => this.closeModal(true)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="15" height="16" viewBox="0 0 15 16">
                        <g fill="none" fill-rule="evenodd" stroke="#B1BAC5" stroke-linecap="round" stroke-width="4">
                            <path d="M2.289 2.3L13 14M12.711 2.3L2 14" />
                        </g>
                    </svg>
                </button>
            </div >
        );
    }
}