import React from "react";
import Lottie from "react-lottie";

import pdpAnimation from '../../public/lotties/loading-pdp-animation.json';

import './ProductDetailsPendingModal.scss';

interface ProductDetailsPendingModalProps {
    onRequestClose: () => void;
}

interface ProductDetailsPendingModalState {

}

export class ProductDetailsPendingModal extends React.Component<ProductDetailsPendingModalProps, ProductDetailsPendingModalState> {

    render() {
        const canvas = {
            width: '408px',
            height: '100%',
            padding: '30px'
        }

        const animation = {
            loop: true,
            autoplay: true,
            animationData: pdpAnimation,
            rendererSettings: {
                className: 'lottie',
                preserveAspectRatio: "xMidYMid slice"
            }
        };

        return (
            <div id="product-details-pending-modal" className="canvas" style={canvas}>
                <div id="close-button" onClick={this.props.onRequestClose}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15">
                        <g fill="none" fill-rule="evenodd" stroke="#232E3B" stroke-linecap="round" stroke-width="2.36">
                            <path d="M2.289 1.3L13 13M12.711 1.3L2 13" />
                        </g>
                    </svg>
                </div>

                <Lottie options={animation} height={120} width={120} />

                <p className="loading-product-details-title">Loading product details...</p>

                <p className="information-title">Information will be updated within the hour</p>

            </div>
        );
    }
}