import React from "react";

import './CouponProgressBar.scss';

interface CouponProgressBarProps {
    width: number;
    progress: number;
}

interface CouponProgressBarState {

}

export class CouponProgressBar extends React.Component<CouponProgressBarProps, CouponProgressBarState> {

    private maxWidthInPixels: number = 500;

    render() {
        let color: string = '';

        if (this.props.progress <= 33) color = '#FF6060';
        if (this.props.progress > 33 && this.props.progress <= 66) color = '#FEE600';
        if (this.props.progress > 66) color = '#3AE0C0';

        const updatedWidth: number = (this.props.progress / 100) * this.maxWidthInPixels;

        return (
            <div id="coupon-progress-bar" style={{ width: this.props.width }}>
                <div className="bar" style={{ width: updatedWidth, backgroundColor: color }}></div>
            </div>
        )
    }
}