import React from 'react';

import { ActivateCashbackModal } from './ActivateCashback/ActivateCashbackModal';
import { CashbackActivatedModal } from './CashbackActivatedModal/CashbackActivatedModal';
import { ContentScriptMixpanelService } from '../services/content-script-mixpanel.service';
import { CheckoutModal } from './Checkout/CheckoutModal';
import { Retailer } from '../../common/interfaces';
import { CouponModal } from './Coupon/CouponModal';
import { MessageSender } from '../../common/messaging/message-sender';
import { FadeSlideAnimation } from '../../common/react/FadeSlideAnimation/FadeSlideAnimation';
import EY from '../../common/EY';

import './EarnyModal.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
interface EarnyModalState {
  show: boolean;
  modal?: any;
  modalName?: string;
}

// ===========================================================================================
// --- EarnyModal
// ===========================================================================================
class EarnyModal extends React.Component<any, EarnyModalState> {

  constructor(props: any) {
    super(props);

    this.state = {
      show: false,
    };

    if (chrome && chrome.runtime.onMessage) {
      chrome.runtime.onMessage.addListener(async (request, sender, sendResponse) => {
        if (request.type === EY.ModalActionTypes.TYPE_SHOW_MODAL && request.modalName) {
          this.showModal(request.modalName, request)
          sendResponse({ success: true });
        }
      });
    }
  }

  showModal = async (modalName: string, modalRequest: any) => {
    const [isLoggedIn, isSubscribed]: [boolean, boolean] = await Promise.all([
      MessageSender.isUserLoggedIn(),
      MessageSender.isUserSubscribed()
    ])

    const retailer: Retailer = modalRequest.retailer;
    let modal: any;

    if (retailer) {
      if (modalName === EY.ModalNames.MODAL_NAME_ACTIVATE_CASHBACK) {

        modal = (
          <ActivateCashbackModal
            retailer={retailer}
            isSubscribed={isSubscribed}
            isLoggedIn={isLoggedIn}
            onRequestClose={(isUserInitiated: boolean) => this.closeModal(modalName, retailer.retailerId, isUserInitiated)} />

        );
      }

      if (modalName === EY.ModalNames.MODAL_NAME_CASHBACK_ACTIVATED) {
        modal = (
          <CashbackActivatedModal
            retailer={retailer}
            cashbackActivatedFrom={modalRequest.source}
            onRequestClose={(isUserInitiated: boolean) => this.closeModal(modalName, retailer.retailerId, isUserInitiated)} />

        );
      }

      if (modalName === EY.ModalNames.MODAL_NAME_CHECKOUT) {
        modal = (
          <CheckoutModal
            retailer={retailer}
            onRequestClose={(isUserInitiated: boolean) => this.closeModal(modalName, retailer.retailerId, isUserInitiated)} />
        );
      }

      if (modalName === EY.ModalNames.MODAL_NAME_COUPON) {
        modal = (
          <CouponModal
            retailer={retailer}
            onRequestClose={(isUserInitiated: boolean) => this.closeModal(modalName, retailer.retailerId, isUserInitiated)} />
        );
      }
    }

    this.setState({ show: true, modal, modalName });
  }

  closeModal = (modalName: string, retailerId: number, isUserInitiated: boolean) => {
    if (isUserInitiated) {
      MessageSender.didDismissModal(modalName, retailerId);
      ContentScriptMixpanelService.trackModalDismissed(this.state.modalName || 'name-undefined');
    }
    this.setState({ show: false, modalName: undefined });
  }

  render() {
    return (
      <>
        <div id="earny">
          {this.state.modalName !== EY.ModalNames.MODAL_NAME_COUPON &&
            <FadeSlideAnimation show={this.state.show} fadeInTranslateY={0} fadeOutTranslateY={-40}>
              <div id="earny-box">
                {this.state.modal}
              </div>
            </FadeSlideAnimation>
          }
        </div>

        {this.state.modal && this.state.modalName === EY.ModalNames.MODAL_NAME_COUPON &&
          <div id="fullscreen-modal">
            <div id="background"></div>
            <FadeSlideAnimation show={this.state.show} fadeInTranslateY={0} fadeOutTranslateY={-500}>
              {this.state.modal}
            </FadeSlideAnimation>
          </div>
        }
      </>
    )
  }
}

export default EarnyModal;
